/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"row\">\n\n    <div class=\"col-md-4\">\n        <mat-card>\n          <mat-card-title><h2>What is CAP?</h2></mat-card-title>\n          <mat-card-content>\n            <p>The Client Assistance Program helps people with disabilities who are seeking or receiving vocational rehabilitation services by providing information about the federal Rehabilitation Act, including: </p>\n\n            <ul style=\"text-align: start;\">\n              <li>How the state can help you get, keep or advance in work </li>\n              <li>How you qualify</li>\n              <li>How you pilot your own rehabilitation</li>\n              <li>The purpose of the Rehab. Act</li>\n              <li>Your rights under the act</li>\n              <li>Assisting individuals who have problems applying for or receiving services under the Act</li>\n            </ul>\n            <img class=\"secondary-image\" src=\"assets/images/сapitol.png\" alt=\"capitol\">\n          </mat-card-content>\n        </mat-card>\n    </div>\n\n\n\n\n       <div class=\"col-4\">\n          <mat-card>\n              <mat-card-title> <h2>What can CAP do for me?</h2></mat-card-title>\n        <mat-card-content>\n        <p>If you have a problem with the Georgia Vocational Rehabilitation Agency or application, the Georgia Client Assistance Program can assist you in many ways, including: </p>\n\n        <ul style=\"text-align: start;\">\n            <li>Advising you of your rights </li>\n            <li>Solving communication problems </li>\n            <li>Teaching you how to make requests </li>\n            <li>Referring you to other agencies where helpful </li>\n            <li>Negotiating for you</li>\n            <li>Mediating disputes</li>\n            <li>Advocating for you with the agency and others </li>\n            <li>Presenting your requests to the agency</li>\n            <li>Obtaining legal representation where we think it appropriate</li>\n          </ul>\n          <img class=\"secondary-image\" src=\"assets/images/woman.png\" alt=\"woman in wheelchair talking\">\n      </mat-card-content>\n    </mat-card>\n    </div>\n \n\n \n      <div class=\"col-4\">\n          <mat-card>\n              <mat-card-title><h2>When do I contact CAP?</h2></mat-card-title>\n        <mat-card-content>\n        <p>You may call the Client Assistance Program for any of the following reasons: </p>\n        <ul style=\"text-align: start;\">\n            <li>You want to know what is required to qualify for rehabilitation services  </li>\n            <li>You believe you qualify for rehabilitation services but are not allowed to complete an application  </li>\n            <li>You disagree with any decision to deny, stop or delay services  </li>\n            <li>You cannot get a written decision on a request you have made  </li>\n            <li>You are not allowed enough choice in your rehabilitation program</li>\n            <li>You have a disability related problem, and do not know where to go for help</li>\n          </ul>\n          <img class=\"secondary-image\" src=\"assets/images/class.png\" alt=\"students in class with teacher\">\n      </mat-card-content>\n    </mat-card>\n  </div>\n\n</div>\n\n\n\n\n<div class=\"row\">\n    <div class=\"col-md-6 full-width\">\n      <mat-card>\n        <mat-card-content>\n             \n            <p>The State of Georgia has privatized the management and operation of the Client Assistance Program to be administered by the Law Offices of Martin and Jones. The staff of the Client Assistance Program includes: </p>\n\n\n            <ul class=\"brochure-list\">\n                <li>Jennifer M. Page, Director</li>\n                <li><a href=\"mailto:gacapdirector@georgiacap.com\">gacapdirector@georgiacap.com</a></li>\n                <li><a href=\"mailto:gacapdirector@georgiacap.com\">jpage@georgiacap.com</a></li>\n                <li>&nbsp;</li>\n                <li>Stephanie Woods, Counselor/Advocate</li>\n                <li><a href=\"mailto:swoods@georgiacap.com\">swoods@georgiacap.com</a></li>\n                <li>&nbsp;</li>\n                <li>Georgia Client Assistance program</li>\n                <li>123 North McDonough Street</li>\n                <li>Decatur, Georgia 30030</li>\n                <li>(404) 373-2040 [Voice]</li>\n                <li>(800) 822-9727 [Toll Free]</li>\n                <li>(404) 373-4110 [FAX]</li>\n                <li>&nbsp;</li>\n                <li><a href=\"http://www.georgiacap.com\">http://www.georgiacap.com</a></li>\n            </ul>\n\n\n\n            \n\n\n                <p>The Law Offices of Martin and Jones administers and conducts all programs and activities in accordance with the U.S. civil rights act which provide that no persons shall, on grounds of race, color, national origin, sex or disability be excluded benefits or be subject to discrimination under any program or activity receiving federal financial assistance. </p>\n\n\n         </mat-card-content>\n      </mat-card>\n    </div>\n    <div class=\"col-md-6 full-width\">\n      <mat-card>\n          <mat-card-content>\n              <mat-card-title><h2 id=\"main-content\">Georgia Client Assistance Program (GaCAP)</h2></mat-card-title>\n              <p>A division of the</p>\n              <mat-card-subtitle><h3 id=\"main-content\">Law Offices of Martin and Jones</h3></mat-card-subtitle>\n              <img alt=\"georgia cap logo\" class=\"brochure-image\" src=\"assets/images/gaCapLogo.svg\">\n             \n              <p>Assisting consumers of the Georgia Vocational Rehabilitation Agency actively participate in their rehabilitation process</p>\n              <p>The GaCAP is a program for Vocational Rehabilitation clients and client applicants who are seeking or receiving services from a project, program, or facility funded under the Rehabilitation Act of 1973 as amended.</p>\n  \n  \n                \n  \n           </mat-card-content>\n      </mat-card>  \n    </div>\n  </div>\n  \n\n   ");

