/* harmony default export */ __webpack_exports__["default"] = ("<mat-card class=\"full-width\">\n  <h2>Frequently Asked Questions</h2>\n  <mat-card-content>\n    \n  <mat-accordion>\n\n    <mat-expansion-panel (opened)=\"panelOpenState = true\" (closed)=\"panelOpenState = false\">\n      <mat-expansion-panel-header [class.darker-hover]=\"!panelOpenState\">\n        <mat-panel-title>\n          Do you provide services, such as housing, child care, or any other monetary services?\n        </mat-panel-title>\n      </mat-expansion-panel-header>\n\n      <p>No. However, CAP can assist with a referral to an agency/organization who provides those services. Contact us: <a href=\"tel:800-822-9727\"><span class=\"visually-hidden\">Toll-free telephone call state wide</span>(800) 822-9727</a> (toll-free), <a href=\"tel:404-373-2040\"><span class=\"visually-hidden\">Atlanta area</span>(404) 373-2040</a> or <a href=\"800-255-0135\"><span class=\"visually-hidden\">Georgia Relay Service</span>(800) 255-0135</a> (Georgia Relay Service)</p>\n\n    </mat-expansion-panel>\n\n    <mat-expansion-panel (opened)=\"panelOpenState = true\" (closed)=\"panelOpenState = false\">\n      <mat-expansion-panel-header [class.darker-hover]=\"!panelOpenState\">\n        <mat-panel-title>\n          I want to open/reopen my case with GVRA. Which office serves my county?\n        </mat-panel-title>\n      </mat-expansion-panel-header>\n\n      <p>Please visit <a href=\"https://gvs.georgia.gov/find-location\">GVRA find a location</a> to search by city, county or zip code or <a href=\"https://gvs.georgia.gov/text-only-location-list\">GVRA text only location list</a> to view a list of all GVRA offices and their counties served.  </p>\n\n    </mat-expansion-panel>\n    \n    <mat-expansion-panel (opened)=\"panelOpenState = true\" (closed)=\"panelOpenState = false\">\n      <mat-expansion-panel-header [class.darker-hover]=\"!panelOpenState\">\n        <mat-panel-title>\n          Can CAP assist with third party entities, such as a provider a client has been sent to by VR?\n        </mat-panel-title>\n      </mat-expansion-panel-header>\n\n      <p>No, but depending on the issue/matter, GVRA’s Provider Relations Manager may be able to assist (<a href=\"tel:404-273-1864\"><span class=\"visually-hidden\">GVRA’s Provider Relations Manager</span>404-273-1864</a>).</p>\n\n    </mat-expansion-panel>\n\n    <mat-expansion-panel (opened)=\"panelOpenState = true\" (closed)=\"panelOpenState = false\">\n      <mat-expansion-panel-header [class.darker-hover]=\"!panelOpenState\">\n        <mat-panel-title>\n          I’ve been denied for a service but need to reach my employment goal. Can CAP help?\n        </mat-panel-title>\n      </mat-expansion-panel-header>\n\n      <p>Interested in becoming a CAP client so we can review your GVRA case file and negotiate on your behalf? If so, visit our “Resources” section to download & complete an Intake and Notice of Representation Form which can be submitted via:<br/>\n        <strong>Email: </strong> <a href=\"mailto:GACAPDirector@georgiacap.com\">GACAPDirector@georgiacap.com</a><br/>\n        <strong>Fax:</strong> (404) 373-4110<br/>\n        <strong>Mail:</strong> 123 N. McDonough Street Decatur, Georgia 30030</p>\n\n    </mat-expansion-panel>\n\n    <mat-expansion-panel (opened)=\"panelOpenState = true\" (closed)=\"panelOpenState = false\">\n      <mat-expansion-panel-header [class.darker-hover]=\"!panelOpenState\">\n        <mat-panel-title>\n          Do you assist with Department of Education cases?\n        </mat-panel-title>\n      </mat-expansion-panel-header>\n\n      <p>No. The Georgia Advocacy Office can be of assistance though. Contact them at <a href=\"tel:404-885-1234\"><span class=\"visually-hidden\">Georgia Advocacy Office</span>404-885-1234</a> or via their <a href=\"http://thegao.org\">website</a>.</p>\n\n    </mat-expansion-panel>\n        \n    <mat-expansion-panel (opened)=\"panelOpenState = true\" (closed)=\"panelOpenState = false\">\n      <mat-expansion-panel-header [class.darker-hover]=\"!panelOpenState\">\n        <mat-panel-title>\n          I received a Notice of Change letter in the mail from GVRA. What does this mean?\n        </mat-panel-title>\n      </mat-expansion-panel-header>\n\n      <p>Notices of Change (NOCs) are official documents used by GVRA to notify individuals of certain decisions and due process rights during the vocational rehabilitation (VR) process.  If your Notice of Change includes:\n        <br/><br/>\n        GVRA Policy Cite: 216.1.07 – Congratulations, GVRA has determined you’re eligible for their services! Contact your counselor to schedule your next appointment.\n        <br/><br/>\n        GVRA Policy Cite: 502.1.13 A-F or 502.1.14 A-S GVRA has either determined you’re ineligible for services and/or that an intervening reason exists for case closure. You have 30 days from the receipt of this letter to appeal your case closure. \n        <br/><br/>\n        To appeal, you’ll need to write and submit a letter and/or email (it doesn’t have to be formal) to your counselor with today’s date stating you disagree with your case being closed. Should you submit your appeal in person, take down the name of the person you turned it in to and ask them for a copy for your records. \n      </p>\n\n    </mat-expansion-panel>\n\n    <mat-expansion-panel (opened)=\"panelOpenState = true\" (closed)=\"panelOpenState = false\">\n      <mat-expansion-panel-header [class.darker-hover]=\"!panelOpenState\">\n        <mat-panel-title>\n            I’m looking for resources for\n            &bull; Legal advice\n            &bull; Filing an ADA complaint\n            &bull; Filing a discrimination complaint\n            &bull; Home modifications\n            &bull; Etc.\n            \n        </mat-panel-title>\n      </mat-expansion-panel-header>\n\n      <p>CAP has a statewide resource list. Contact us: <a href=\"tel:800-822-9727\"><span class=\"visually-hidden\">Toll-free telephone call state wide</span>(800) 822-9727</a> (toll-free), <a href=\"tel:404-373-2040\"><span class=\"visually-hidden\">Atlanta area</span>(404) 373-2040</a> or <a href=\"800-255-0135\"><span class=\"visually-hidden\">Georgia Relay Service</span>(800) 255-0135</a> (Georgia Relay Service)</p>\n\n    </mat-expansion-panel>\n            \n    <mat-expansion-panel (opened)=\"panelOpenState = true\" (closed)=\"panelOpenState = false\">\n      <mat-expansion-panel-header  [class.darker-hover]=\"!panelOpenState\">\n        <mat-panel-title>\n          GVRA says they are closing my case, but I still need services. What can I do?\n        </mat-panel-title>\n      </mat-expansion-panel-header>\n\n      <p>If you’re interested in becoming a CAP client, we can review your GVRA case file and negotiate on your behalf.  Please visit our “Resources” section to download & complete an Intake and Notice of Representation Form which can be submitted via:<br/>\n        <strong>Email: </strong> <a href=\"mailto:GACAPDirector@georgiacap.com\">GACAPDirector@georgiacap.com</a><br/>\n        <strong>Fax:</strong> (404) 373-4110<br/>\n        <strong>Mail:</strong> 123 N. McDonough Street Decatur, Georgia 30030</p>\n\n    </mat-expansion-panel>\n\n    <mat-expansion-panel (opened)=\"panelOpenState = true\" (closed)=\"panelOpenState = false\">\n      <mat-expansion-panel-header  [class.darker-hover]=\"!panelOpenState\">\n        <mat-panel-title>\n          My counselor is not responding to my phone calls or emails. Can you help?\n        </mat-panel-title>\n      </mat-expansion-panel-header>\n\n      <p>Yes. However, if you haven’t already, contact GVRA’s Customer Care Unit (<a href=\"tel:844-367-4872\"><span class=\"visually-hidden\">GVRA’s Customer Care Unit</span>844-367-4872</a>) to inform them of your situation.<br/>If Customer Care doesn’t reestablish communication with your counselor, Contact us: <a href=\"tel:800-822-9727\"><span class=\"visually-hidden\">Toll-free telephone call state wide</span>(800) 822-9727</a> (toll-free), <a href=\"tel:404-373-2040\"><span class=\"visually-hidden\">Atlanta area</span>(404) 373-2040</a> or <a href=\"800-255-0135\"><span class=\"visually-hidden\">Georgia Relay Service</span>(800) 255-0135</a> (Georgia Relay Service)</p>\n\n    </mat-expansion-panel>\n                \n    <mat-expansion-panel (opened)=\"panelOpenState = true\" (closed)=\"panelOpenState = false\">\n      <mat-expansion-panel-header  [class.darker-hover]=\"!panelOpenState\">\n        <mat-panel-title>\n          I want to file a complaint about\n          &bull; My counselor\n          &bull; My provider\n          &bull; GVRA            \n        </mat-panel-title>\n      </mat-expansion-panel-header>\n\n      <p>Contact GVRA’s Customer Care Unit (<a href=\"tel:844-367-4872\"><span class=\"visually-hidden\">GVRA’s Customer Care Unit</span>844-367-4872</a>) to inform them of your particular concern/situation.</p>\n\n    </mat-expansion-panel>\n\n    <mat-expansion-panel (opened)=\"panelOpenState = true\" (closed)=\"panelOpenState = false\">\n      <mat-expansion-panel-header  [class.darker-hover]=\"!panelOpenState\">\n        <mat-panel-title>\n        I want to sue GVRA. Can CAP assist me?\n        </mat-panel-title>\n      </mat-expansion-panel-header>\n\n      <p>No, you’ll need to hire a personal attorney.  If you’re interested in becoming a CAP client, we can review your GVRA case file and negotiate on your behalf.  Please visit our “Resources” section to download & complete an Intake and Notice of Representation Form which can be submitted via:<br />\n        <strong>Email: </strong> <a href=\"mailto:GACAPDirector@georgiacap.com\">GACAPDirector@georgiacap.com</a><br/>\n        <strong>Fax:</strong> (404) 373-4110<br/>\n        <strong>Mail:</strong> 123 N. McDonough Street Decatur, Georgia 30030</p>\n\n    </mat-expansion-panel>\n                    \n    <mat-expansion-panel (opened)=\"panelOpenState = true\" (closed)=\"panelOpenState = false\">\n      <mat-expansion-panel-header  [class.darker-hover]=\"!panelOpenState\">\n        <mat-panel-title>\n          Are CAP brochures available in alternative formats?\n        </mat-panel-title>\n      </mat-expansion-panel-header>\n\n      <p>Yes, they’re available upon request.</p>\n\n    </mat-expansion-panel>\n\n  </mat-accordion>\n\n  </mat-card-content>\n</mat-card>");

