/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"row\" id=\"home\">\n  <div class=\"col-12\">\n    <mat-card>\n      <mat-card-content>\n        <h1>The Georgia Client Assistance Program</h1>\n        <img class=\"header-image\" src=\"assets/images/gaCapLogo.svg\" alt=\"Georgia Client Assistance Program\">\n        <p><strong>A division of the Law Offices of Martin and Jones</strong></p>\n        <p>Jennifer Page - CAP Director <br/>\n        Stephanie Woods MS, CRC - Counselor/Advocate</p>\n      </mat-card-content>\n    </mat-card>\n  </div>\n</div>\n\n<div class=\"row\">\n  <div class=\"col-md-6 full-width\">\n    <mat-card>\n      <mat-card-content>\n        <img class=\"main-page-image\" src=\"assets/images/reading.jpg\" alt=\"reading braille in the park\">\n      </mat-card-content>\n    </mat-card>\n  </div>\n  <div class=\"col-md-6 full-width\">\n    <mat-card>\n      <mat-card-content>\n          <h2>Welcome to the Georgia Client Assistance Program</h2>\n          <p>Thank you for visiting the Georgia Client Assistance Program (CAP).</p>\n          <p>The CAP is for Vocational Rehabilitation clients and client applicants who are seeking or receiving services from a project, program or facility funded under the Rehabilitation Act.</p>\n      </mat-card-content>\n    </mat-card>  \n  </div>\n</div>\n\n<div class=\"row\">\n  <div class=\"col-12\">\n    <mat-card class=\"full-width\">\n      <h2 id=\"main-content\">What is the Client Assistance Program?</h2>\n      <mat-card-content>\n        <p>Client Assistance Programs are funded by the U. S. Department of Education in all states as part of grants for Vocational Rehabilitation of individuals with disabilities. The Georgia Department of Labor Vocational Rehabilitation Program has privatized the management and operation of the Client Assistance Program to be administered by Martin and Jones. The Client Assistance Program, helps people with disabilities who are seeking or receiving vocational rehabilitation services by:</p>\n        <div class=\"row\" style=\"text-align: start;\">\n          <div class=\"col-md-6\">\n            <p><strong>Providing information</strong> about the federal Rehabilitation Act, including:</p>\n            <ul>\n              <li>How the state can help you get, keep or advance in work</li>\n              <li>How you qualify</li>\n              <li>How you pilot your own rehabilitation</li>\n              <li>The purpose of the Rehabilitation Act</li>\n              <li>Your rights under the Act</li>\n            </ul>\n            <p><strong>Assisting</strong> individuals who have problems applying for or receiving services under the Act.</p>\n            <p><strong>Teaching</strong> you how to make requests</p>      \n          </div>\n          <div class=\"col-md-6\">\n            <p><strong>Referring</strong> you to other agencies where helpful</p>\n            <p><strong>Negotiating</strong> for you</p>\n            <p><strong>Mediating</strong> disputes</p>\n            <p><strong>Advocating</strong> for you with the agency and others</p>\n            <p><strong>Presenting</strong> your requests to the agency</p>\n            <p><strong>Obtaining</strong> legal representation where we think it appropriate</p>\n          </div>\n        </div>\n      </mat-card-content>\n    </mat-card>\n  </div>\n</div>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n    <mat-card class=\"full-width image-col\">\n      <mat-card-content>\n        <img class=\"secondary-image\" src=\"assets/images/signing.jpg\" alt=\"having a conversation in sign language\">\n      </mat-card-content>\n    </mat-card>\n  </div>\n  <div class=\"col-md-6\">\n    <mat-card class=\"full-width image-col\">\n      <mat-card-content>\n        <img class=\"secondary-image\" src=\"assets/images/couple.jpg\" alt=\"couple smiling\">\n      </mat-card-content>\n    </mat-card>\n  </div>\n</div>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n    <mat-card class=\"full-width\">\n      <h2>What can the Client Assistance Program do for me?</h2>\n      <mat-card-content>\n        <p>If you have a problem with your vocational rehabilitation program or application, the Client Assistance Program can assist you in many ways, including:</p>\n        <ul style=\"text-align: start;\">\n          <li>Advising you of your rights</li>\n          <li>Solving communication problems</li>\n          <li>Teaching you how to make requests</li>\n          <li>Referring you to other agencies where helpful</li>\n          <li>Negotiating for you</li>\n          <li>Mediating disputes</li>\n          <li>Advocating for you with the agency and others</li>\n          <li>Presenting your requests to the agency</li>\n          <li>Obtaining legal representation where we think it appropriate</li>\n        </ul>\n      </mat-card-content>\n    </mat-card>\n  </div>\n  <div class=\"col-md-6\">\n    <mat-card class=\"full-width\">\n      <h2>When and how do I contact the Client Assistance Program?</h2>\n      <mat-card-content>\n        <p>You may call the Client Assistance Program for any of the following reasons:</p>\n        <ul style=\"text-align: start;\">\n          <li>You want to know what is required to qualify for rehabilitation services</li>\n          <li>You believe you qualify for rehabilitation services but are not allowed to complete an application</li>\n          <li>You disagree with any decision to deny, stop or delay services</li>\n          <li>You cannot get a written decision on a request you have made</li>\n          <li>You are not allowed enough choice in your rehabilitation program</li>\n          <li>You have a disability-related problem, and do not know where to go to get help</li>\n        </ul>\n      </mat-card-content>\n    </mat-card>\n  </div>\n</div>\n\n<div class=\"row\" id=\"resources\">\n  <div class=\"col-12\">\n    <mat-card class=\"full-width\">\n      <h2>Resources</h2>\n      <mat-card-subtitle>Click to download</mat-card-subtitle>\n      <mat-card-content>\n        <div class=\"row\">\n          <div class=\"col-md-6\">\n            <p><a href=\"/brochure\">CAP Brochure</a></p>\n            <p><a href=\"assets/files/CAP Intake info.docx\" download>CAP Intake Information (.DOCX)</a></p>\n          </div>\n          <div class=\"col-md-6\">\n            <p><a href=\"assets/files/JP CAP NOR.docx\" download>Notice of Representation - Jennifer Page (.DOCX)</a></p>\n            <p><a href=\"assets/files/SW CAP NOR.docx\" download>Notice of Represenetation - Stephanie Woods (.DOCX)</a></p>\n          </div>\n        </div>\n      </mat-card-content>\n    </mat-card>\n  </div>\n</div>\n\n<div class=\"row\">\n  <div class=\"col-md-6\">\n    <mat-card class=\"full-width image-col\">\n      <mat-card-content>\n        <img class=\"secondary-image\" src=\"assets/images/student.jpg\" alt=\"students in class\">\n      </mat-card-content>\n    </mat-card>\n  </div>\n  <div class=\"col-md-6\">\n    <mat-card class=\"full-width image-col\">\n      <mat-card-content>\n        <img class=\"secondary-image\" src=\"assets/images/laptop.jpg\" alt=\"smiling woman in a wheelchair using her computer\">\n      </mat-card-content>\n    </mat-card>\n  </div>\n</div>\n\n<div class=\"row\" id=\"contact\">\n  <div class=\"col-12\">\n    <mat-card>\n      <h2>Contact</h2>\n      <mat-card-content>\n        <div class=\"row\">\n          <div class=\"col-md-6\">\n            <p>\n              <strong>Toll-free telephone call state wide:</strong><br/>\n              <a href=\"tel:800-822-9727\"><span class=\"visually-hidden\">Toll-free telephone call state wide</span>(800) 822-9727</a>\n            </p>\n            <p>\n              <strong>Atlanta area:</strong><br/>\n              <a href=\"tel:404-373-2040\"><span class=\"visually-hidden\">Atlanta area</span>(404) 373-2040</a><br/>\n            </p>\n            <p>\n              <strong>Georgia Relay Service:</strong><br/>\n              <a href=\"tel:800-255-0135\"><span class=\"visually-hidden\">Georgia Relay Service</span>(800) 255-0135</a>\n            </p>\n          </div>\n          <div class=\"col-md-6\">\n            <p>\n              <strong>Address:</strong><br/>\n              <a href=\"https://goo.gl/maps/Sk8FajCk7122\" target=\"_blank\">123 N. McDonough Street<br/>\n              Decatur, Georgia 30030</a>\n            </p>\n            <p>\n              <strong>Email:</strong><br/>\n              <a href=\"mailto:GACAPDirector@georgiacap.com\">GACAPDirector@georgiacap.com</a>\n            </p>\n            <p><strong>Fax:</strong><br/>\n              (404) 373-4110\n            </p>\n          </div>\n        </div>\n      </mat-card-content>\n    </mat-card>\n  </div>\n</div>\n\n<div class=\"row\" id=\"faq\">\n  <div class=\"col-12\">\n    <app-faq></app-faq>\n  </div>\n</div>");

